$form-border-color: rgb(129, 183, 255);
$form-border-color-hover: rgb(64, 147, 255);
$form-border-color-focus: rgb(64, 147, 255);

form {
    //flex-grow: 1;
    min-width: 320px;
    width: 100%;
    .form-group {
        display: flex;
        padding: 20px 10px;

        input {
            font-size: 16px;
            flex-grow: 1;
            border: none;
            border-bottom: 2px solid $form-border-color;
            padding: 0.5rem;
            outline: none;

            &:last-child {
                margin-right: 0px;
            }

            &:hover {
                border-bottom: 2px solid $form-border-color-hover;
            }

            &:focus {
                border-bottom: 2px solid $form-border-color-focus;
            }
        }

        textarea {
            resize: none;
            border-radius: 3px;
            font-size: 16px;
            padding: 10px;
            flex-grow: 1;
            border: 2px solid $form-border-color;
            outline: none;

            &:hover {
                border: 2px solid $form-border-color-hover;
            }

            &:focus {
                border: 2px solid $form-border-color-focus;
            }
        }
    }
}

@media screen and (max-width: 776px) {
    form {
        .form-group {
            flex-direction: column;
            
            input {
                &:nth-child(2n) {
                    margin-top: 40px;
                }
            }
        }
    }
}

@media screen and (min-width: 776px) {
    form {
        .form-group {
            input {
                
                &:nth-child(2n) {
                    margin-left: 20px;
                }
            }
        }
    }
}