.custom-button {
  cursor: pointer;
  color: #fff;
  background-color: rgba(3, 3, 3, 0.78);
  border-radius: 3px;
  border: none;
  padding: 1rem 10%;
  outline: none;

  &:hover {
    background-color: rgba(103, 106, 0, 0.78);
  }

  &:active {
    background-color: rgba(103, 106, 0, 0.78);
  }
}

.fluid {
  width: 100%;
}
