@import "../../variabless.scss";

.content {
  padding: 0px 10px;
  display: flex;
  justify-content: center;

  .content-helper {
    margin: 20px 0px;
    width: 1170px;
    height: 100%;
    max-height: auto;
    max-width: 900px;
  }

  section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 10%;
    padding-bottom: 40%;

    &:nth-child(1n) {
      .headline {
        margin-bottom: 50px;
      }
    }

    .headline {
      font-weight: bold;
    }

    span {
      color: rgba(188, 194, 0, 0.78);
    }

    .about-block {
      display: flex;
      font-size: 18px;

      .description {
        p {
          padding-right: 10px;
          margin: 30px 0px;
        }
      }

      .image {
        display: flex;
        align-items: center;
      }
    }

    img {
      size: 25% 25%;
      object-fit: cover;
      max-width: 300px;
      height: 300px;
      border-radius: 100%;
    }
  }

  .title {
    font-size: 20px;
    font-weight: bold;
  }

  .contact-block {
    display: flex;
    flex-wrap: wrap;

    form {
      flex-grow: 1;
    }

    img {
      padding: 1rem;
      border-radius: 0;
      max-width: 150px;
      height: 150px;
      display: block;
      float: left;
    }
  }

  .project-block {
    .project-group {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      //grid-template-rows: 1fr 1fr;
      gap: 40px;
    }

    .project-item {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;

      .title {
        margin: 10px 0px;
      }

      .image {
        img {
          width: 100%;
          height: 200px;
          border-radius: 20px;
          max-width: 100%;
        }
      }

      .description {
        max-width: 400px;
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
        padding: 10px;
      }
    }

    .button-wrapper {
      margin-top: 20px;
      display: flex;
      justify-content: center;

      a {
        text-decoration: none;
      }
    }
  }

  .technology-block {
    display: grid;
    grid-gap: 40px;
    row-gap: 80px;

    .tech-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;

      .icon {
        margin: 20px 0px;

        i {
          color: rgb(66, 68, 73);
          font-size: 120px;
        }
      }

      .description {
        text-align: center;
      }

      .image {
        img {
          padding: 1rem;
          max-height: 150px;
          border-radius: 0;
        }
      }
    }
  }
}

@media screen and (min-width: 776px) {
  .content {
    section {
      &:first-child {
        .headline {
          margin-bottom: 0px;
        }
      }

      .headline {
        font-size: 50px;
      }
    }
  }

  .technology-block {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 776px) {
  .content {
    .about-block {
      align-items: center;
      flex-direction: column;

      h3 {
        text-align: center;
      }

      .headline {
        padding-bottom: 20px;
      }
    }

    .description {
      order: 1;
      max-width: 500px;

      p {
        padding: 5px;
      }
    }
  }
}

@media screen and (max-width: 776px) {
  .content {
    .headline {
      text-align: center;
      margin-bottom: 10px;
      font-size: 35px;
    }

    .contact-block {
      align-items: center;
      flex-direction: column;

      h3 {
        text-align: center;
      }
    }
  }
}

@media screen and (max-width: 776px) {
  .content {
    .project-block {
      .project-group {
        display: flex;
        flex-direction: column;
      }

      .description {
        margin: 0px 10px;
      }

      .image {
        padding: 10px;
        //width: 400px;
        max-width: 400px;
      }
    }

    .technology-block {

      .tech-item {
        margin: 20px 0px;
      }
    }
  }
}

@media screen and (min-width: 567px) and (max-width: 776px) {
  .technology-block {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 567px) {
  .technology-block {
    grid-template-columns: repeat(1, 1fr);
  }
}
